form {
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 90%;
  padding: 20px;
}

.inp {
  width: 50%;
  height: 50px;
}

input {
  width: 100%;
  height: 100%;
  padding: 2%;
  border-radius: 10px;
  font-size: 1em;
}

.button-box {
    display: flex;
    width:52.5%;
    justify-content: space-between;
  }

.btn {
  width: 30%;
  height: 60px;
  background: blue;
  border-radius: 20px;
  font-size: 1em;
}



.cancel-btn {
  width: 30%;
  height: 60px;
  background: blue;
  border-radius: 20px;
  font-size: 1em;
}
